/** Media Queries **/
.informacion {
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.informacion legend {
  color: #2c2c51;
  font-size: 2rem;
  font-weight: bold;
}
.informacion .icon {
  width: 40px;
}
.informacion h4 {
  display: flex;
  flex-flow: row;
  align-items: center;
}

.division {
  display: flex;
  flex-flow: column;
}
@media (min-width: 768px) {
  .division {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.boton-cancelar {
  display: block;
  background-color: green;
  padding: 1.5rem;
  border-radius: 1rem;
  color: aliceblue;
  font-weight: 900;
  font-size: 2rem;
  border: none;
}
.boton-cancelar:hover {
  cursor: pointer;
}

.elementos-estado-riego {
  display: flex;
  flex-direction: column;
  margin-left: 2rem;
}
.elementos-estado-riego input {
  width: max-content;
  margin: auto;
}

.sub_medidor {
  text-align: center;
}