@forward '../base/mixins';
@use '../base/mixins' as m;


.informacion{
    legend{
        color: #2c2c51;
        font-size: 2rem;
        font-weight: bold;
        
    }
    margin-top: 2rem;
    margin-bottom: 2rem;

    .icon{
        width: 40px;

    }

    h4{
        display: flex;
        flex-flow: row;
        align-items: center;
    }


    
}

.division{
    display: flex;
    flex-flow: column;
    

    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap: 2rem;
    } 
}

.boton-cancelar{
    display: block;
    background-color: green;
    padding: 1.5rem;
    border-radius: 1rem;
    color: aliceblue;
    font-weight: 900;
    font-size: 2rem;
    border: none;
    &:hover{
        cursor: pointer;
    }
}

.elementos-estado-riego{
    display: flex;
    flex-direction: column;
    margin-left: 2rem;

    input{
        width: max-content;
        margin: auto;
    }
}

.sub_medidor{
    text-align: center;
    
}